header {
    /*viewport height*/
    height: 888px;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*------------- CV -----------*/

.cv {
    padding-top: 8rem;
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*------------- Header Socials -----------*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    bottom: 3rem;
    left: 0;

}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*------------ Image Me -----------------*/
.me {
    background: linear-gradient(var(--color-primary), #666A73, #353540, transparent);
    width: 22rem;
    height: 30rem;
    margin-left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0rem 0rem;
    
}

/*-------------- SCROLL DOWN ---------------*/
.switch {
    position: fixed;
    right: 4rem;
    bottom: 3rem;

}

/* Media querys (Medium devices) */
@media screen and (max-width: 1024px){
    header {
        height: 68vh;
    }
}

/* Media querys (Small devices) */
@media screen and (max-width: 600px){
    .header__socials, .switch {
        display: none;
    }
    .me {
        width: 75%;
        height: auto;
        margin-left: calc(13%);
    }
}