.container__switch {
    text-align: center;
    position: fixed;
    padding: 1rem 1.7rem;
    
    right: 0%;

    bottom: 2rem;
    display: block;

  }
  .toggle-switch {
    position: relative;
    width: 105px;
    display: inline-block;
    text-align: left;
    top: 8px;
  }
  .checkbox {
    display: none;
  }
  .label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    border: 0 solid var(--color-primary);
    border-radius: 20px;
  }
  .inner {
    display: block;
    width: 200%;
    margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
  }
  .inner:before,
  .inner:after {
    float: left;
    width: 50%;
    height: 36px;
    padding: 0;
    line-height: 36px;
    color: var(--color-primary);
    font-weight: bold;
    box-sizing: border-box;
  }
  .inner:before {
    content: "English";
    padding-left: 10px;
    background-color: var(--color-bg);
    color: var(--color-primary);
    border: 2px solid var(--color-primary);
    border-radius: 20px;
  }
  .inner:after {
    content: "Spanish";
    padding-right: 10px;
    background-color: #bbb;
    color: var(--color-bg);
    text-align: right;
    border: 2px solid var(--color-bg);
    border-radius: 20px;
  }
  .switch {
    display: block;
    width: 24px;
    margin: 5px;
    background: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 66px;
    border: 0 solid #bbb;
    border-radius: 20px;
    transition: all 0.3s ease-in 0s;
  }
  .checkbox:checked + .label .inner {
    margin-left: 0;
  }
  .checkbox:checked + .label .switch {
    right: 0px;
  }